.root {
  flex-grow: 0;
  color: #fff !important;
  align-items: left;
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}
.landing-background {
  background-image: url('../../assests/landing.jpeg');
  background-position: 50% 50%;
  background-size: cover;
  background-size: 100% 100%;
  background-color: var(--white);
  overflow: hidden;
  width: 100%;
  background-repeat: no-repeat;
  height: 1278px;
  position: relative;
}

.login-button {
  align-items: center;
  background-color: #ffa80c;
  border-radius: 52px;
  display: flex;
  padding: 5px 11px;
  position: relative;
}

.div {
  height: 24px;
  position: relative;
  width: 125px;
}

.user-icon {
  height: 24px !important;
  left: 0 !important;
  position: absolute !important;
  top: 0 !important;
  width: 24px !important;
  margin-left: 5%;
  color: #ffffff;
}

.dashboard {
  color: #ffffff;
  font-family: 'Roboto', Helvetica;
  /* font-size: 24px; */
  font-weight: 400;
  left: 48px;
  letter-spacing: 0;
  line-height: normal;
  position: absolute;
  top: 4px;
  white-space: nowrap;
}

.text-welcome {
  color: #ffffff;
  font-family: 'Roboto', Helvetica;
  font-size: 60px;
  font-weight: 500;
  height: 100%;
  left: 100px;
  letter-spacing: 0;
  line-height: normal;
  position: absolute;
  top: 252px;
  white-space: nowrap;
}

.text-employee-management {
  color: #ffa80c;
  font-family: 'Roboto', Helvetica;
  font-size: 80px;
  font-weight: 800;
  height: 100%;
  left: 100px;
  letter-spacing: 0;
  line-height: 79.2px;
  position: absolute;
  top: 330px;
}

@media screen and (max-width: 1280px) {
  .text-welcome {
    font-size: 60px;
    font-weight: 500;
    left: 100px;
    top: 252px;
  }
  .text-employee-management {
    font-size: 80px;
    font-weight: 800;
    left: 100px;
    top: 330px;
  }
  .landing-background {
    height: 1278px;
  }
}
@media screen and (max-width: 960px) {
  .text-welcome {
    font-size: 40px;
    font-weight: 300;
    left: 25px;
    top: 252px;
  }
  .text-employee-management {
    font-size: 60px;
    font-weight: 300;
    left: 25px;
    top: 330px;
  }
  .landing-background {
    height: 1000px;
  }
}
